.wrapper {
  position: absolute;
  top: -32px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  height: 28px;
  font-size: 12px;
  line-height: 28px;
  font-style: normal;
  color: #fff;
  background-color: rgba(20, 20, 20, 0.9);
  border-radius: 16px;
  opacity: 0;
  visibility: hidden;
  transition: .1s ease opacity, .1s ease visibility;
  cursor: pointer; }
  .wrapper::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 4px; }



